body {
	color: $text;
}

a, a>i {
    
	color: $green-dark;

	&:visited {
		color: $green-dark;
	}

	&:hover,
	&:active {
		color: darken($green-dark, 15%);
		text-decoration: none;
	}
}

.breadcrumb {
	margin-bottom: 0;
}

.breadcrumb-item+.breadcrumb-item {
	&:before {
		content: "/";
		font-weight: bold;
		width: 20px;
		text-align: center;
	}
}

.main-header {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-top: 25px;
	margin-bottom: 25px;

	.line {
		width: 100%;
		height: 4px;
		background-color: #e3e3e3;
		margin-left: 50px;
	}
}


.linearcards {
	background-color: $green-light;
	padding-top: 30px;
	padding-bottom: 30px;

	.-card {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: row;
	}
}

.huge-icon {
	font-size: 70px;
}

table>tfoot,
.table>thead,
.table>tbody {

	tr,
	th {
		min-height: 55px;
	}

	.area-btn-trash {
		min-width: 60px;
		padding: 0px;
	}

	.table-btn-trash {
		display: none;
	}

	tr:hover {
		.table-btn-trash {
			display: inline-block;
		}
	}
}

.header-area {
	display: flex;
	justify-content: stretch;
	align-items: center;
}

#searchUserList{
    padding-left: 25px;
}
